#learner-container{
    height: 80%;
    width: 100%;
    background-color: #EBF3FE;
    display: flex;
    
    align-items: center;

}
#learner-img{
    width: 50%;
    padding-left: 60px;
}
#learner-description{
    width: 45%;
}
#learner-title{
    font-family: "Source Sans Pro", Arial, sans-serif;
    font-size: 40px;
    line-height: 52px;
    padding-bottom: 20px;
   
}
#learner-body{
    font-family: "Source Sans Pro", Arial, sans-serif;
    font-size: 18px;
    line-height: 28px;
}
#coursera-business-button{
    height: 56px;
    width:220px;
    border-radius: 4px;
    border:2px solid #0156D2;
    color: #0156D2;;
    background-color: white;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
}
#learner-join-button{
    height: 56px;
    width:220px;
    border-radius: 4px;
   
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
}
#buttons{
    display: flex;
    padding-top: 20px;
    justify-content: flex-start;
}