}
#brand-header{
    font-family: "Source Sans Pro", Arial, sans-serif;
    font-size: 28px;
    font-weight: 600;
    line-height: 36px;
    padding-bottom: 5%;
}
#brands{
    display: flex;
    justify-content: space-evenly;
    width: 80%;
    height: 10%;
}